let order_num = '' // 订单编号
let page = 1 // 页码
let providerList = []; // 支付通道
// let paydataYue = ''
let backindex = ''
import txpwd from '../../components/txpwd.vue'
import uniPopup from '../../components/uni-popup.vue';
export default {
    components:{
        uniPopup,
        txpwd
    },
    onShow(){
        this.getMyorderinfo()
    },
    onLoad(option){
        order_num = option.order_num
        backindex = option.backindex


































    },
    onBackPress() {  
        console.log(backindex)
        if(backindex == 2) {
            uni.switchTab({
                url: '/pages/tabBar/Cart'
            });
            return true
        } 
    },  
    data(){
        return {
            orderInfo:{}, // 订单详情
            valShow:false,
            valueStatus: 0,  // 更新页面状态 防止页面卡死
        }
    },
    watch: {
        valShow(){
            
            this.statusMath(this)
        }
    },
    methods: {
        exchange (ordernum) {
            console.log(ordernum)
            this.$Dialog.confirm({
                message: '确认退款吗？'
            }).then(() => {
                this.$loading()
                this.$http.exchange({
                    order_num: ordernum
                }).then(res => {
                    if (res.status === 200) {
                        this.$toast(res.mess)
                    }
                })
            })
        }, 
        getbacktel(){
            console.log(backindex)
            if(backindex == 2) {
                uni.switchTab({
                    url: '/pages/tabBar/Cart'
                });
                
            } else {
                this.getback()
            }
        },
        /**
         * 确认收货
         */
        // getqrshouhuo(){
        //     this.$showModal({content:'确认收货吗?'}).then(res=>{
        //         if(res.confirm) {
        //             this.$http.getqrshouhuo({order_num:order_num}).then(result=>{
        //                 if(result.status == 200) {
        //                     this.$toast(result.mess)
        //                     setTimeout(()=>{
        //                         this.getback()
        //                     },1000)
        //                 }
        //             })
        //         }
        //     })
            
        // },
        getMyorderinfo(){
            this.$loading()
            this.$http.getMyorderinfo({order_num}).then(res=>{
                if(res.status == 200) {
                    this.orderInfo = res.data
                } 
            })
        },
        /**
         * 取消订单
         */
        cancellation(){
            this.$showModal({content:'确认取消订单吗？'}).then(res=>{
                if(res.confirm) {
                    this.$loading()
                    this.$http.getMyOrderquxiao({order_num}).then(res=>{
                        if(res.status == 200) {
                            this.$toast(res.mess)
                            this.getback()
                        }
                    })
                }
            })
            
        },
        /**
         * 获取支付订单数据
         */
        orderInfoStr(typeIndex,pwd){
            let dataType = {
                order_nums:[order_num],
                zf_type:typeIndex

            }
            return new Promise(reslove=>{
                this.$http.getzhifuorder(dataType).then(res=>{
                    if(res.status == 200) {
                        this.$http.getzhifu({
                            order_number: res.data.order_number,
                            zf_type:res.data.zf_type,
                            pay_password:pwd
                        }).then(res=>{
                            console.log(JSON.stringify(res),'888888888888')
                             reslove(res)
                         })  
                    }else {
                        this.$toast(res.mess)
                    }
                })
            })
            
        },
        /**
         * 支付
         */
        async getpay(index){
            let orderInfo = ''
            if(this.address == ''){
                this.$toast('请选择收货地址')
                return false
            }
            // if(providerList.length <= 0){
            //     this.$toast('获取支付通道失败')
            //     return false
            // }
            
            let requestPaymentid = ''
            if(index == 1)  {
                // 支付宝支付
                requestPaymentid = providerList[0].id
                orderInfo = await this.orderInfoStr(1)
               
            } else if(index == 2) {
                // 微信支付
                requestPaymentid = providerList[1].id
                orderInfo = await this.orderInfoStr(2)
               
            }else {
                // 余额
                this.$http.getpdpaypwd().then(res=>{
                    if(res.status == 200) {
                        if(res.data.zhifupwd == 0) {
                            this.$toast('请先设置支付密码')
                            setTimeout(()=>{
                                this.getnav('/pages/personalSon/setupZfpwd')
                            },1000)
                            
                        } else {
                            this.$refs.popupRef.close() // 关闭
                            this.valShow = true

                        }
                    }
                })
                return false
            }

            uni.requestPayment({
                provider: requestPaymentid,
                orderInfo: orderInfo.data.wxpayinfos,
                success: (e) => {
                    
                    uni.showToast({
                        title: "感谢您的赞助!"
                    })
                    
                },
                fail: (e) => {
                    console.log("fail", e);
                    uni.showModal({
                        content: "支付失败,原因为: " + e.errMsg,
                        showCancel: false
                    })
                },
                complete: () => {
                    this.$refs.popupRef.close() // 关闭
                    
                }
            })
        },
        /**
         * 
         */
        hidePopup(){
            this.$refs.popupRef.show() 
        },
        /**
         * 余额支付
         */
        async codeShow(index) { // 接受子组件传的值  点击的六位密码值
            /**
             * 确认支付
             */
            this.$loading()
            let  paydata = await this.orderInfoStr(3,index.cord)

            if(paydata.status == 200) {
                this.$toast(paydata.mess)
                setTimeout(()=>{
                    this.getback()
                },1000)
            }else {
                this.$toast(paydata.mess)
            }
            
            console.log(paydata)
            
        },
        hideAl(bal) {
            this.valShow = false
           
        },

    },
}
